*, ::before, ::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    width: 100%;
    font-family: 'montserrat', sans-serif;
    background-color: #f4f4f4;
    color: #141414;
    position: relative;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
}

    .GroundZero {
        width: 100%;
        max-width: 1920px;
        display: flex;
        justify-content: center;
        position: relative;
    }

        .Content_Wrap {
            width: 70%;
            margin: 100px 0px;
            margin-bottom: 87vh;
            z-index: 20;
            position: relative;
        }

a {
    text-decoration: none;
    color: #141414;
}

ul {
    list-style-type: none;
}

.SectionTitle {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 60px;
}

.Active_Page {
    color: #0700ff;
}

.Color-Blue {
    color: #0700ff;
    font-family: vox, sans-serif;
    font-weight: 700;
    font-style: normal;
}

.Color-Black {
    color: #141414;
}

@media screen and (max-width: 1100px){
    .Content_Wrap {
        width: 100%;
        padding: 0 80px;
    }
}

@media screen and (max-width: 850px){
    // .Content_Wrap {
